import React from 'react';
import {Chart as ChartJS, registerables} from 'chart.js';
import { Line } from 'react-chartjs-2';
// import 'chartjs-adapter-date-fns';

const LineChart = (props) => {
    var data = {
        labels: props.x,
        datasets: [{
            label: props.title,
            data: props.y,
            backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                'rgba(153, 102, 255, 0.2)',
                'rgba(255, 159, 64, 0.2)'
            ],
            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(255, 159, 64, 1)'
            ],
            borderWidth: 1
        }]
    };

    var options = {
        responsive: true,
        aspectRatio: 0.5,
        
        legend: { display: false },
        scales: {
            x: {
                title: {
                    display: true,
                    text: props.xName,
                },
                grace: "5%",
            },
  
            y: {
                title:{
                    display: true,
                    text: props.yName
                },
                beginAtZero: true,
                grace: "5%"
            },
        },
      };
  return (
    <div className="chart">
        <h3 className='chartTitle'>Line Chart</h3>
        <Line
        data={data}
        options={options}
        />
    </div>
  )
}

export default LineChart