import React from "react";
import NavBar from "./NavBar";
import "./ControlPanel.css";
import { useState, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import fileDownload from "js-file-download";
import loginStausChecker from "./utils/loginStausChecker";
import fetchMachineNames from "./utils/fetchMachineNames";
import addSensor from "./utils/addSensor";
import getAllUsers from "./utils/getAllUsers";
import offsetBody from "./utils/offsetBody";
function ControlPanel(props) {
	const navigate = useNavigate();
	const [currentPassword, setCurrentPassword] = useState("");
	const [newPassword, setNewPassword] = useState("");
	const [retypedPassword, setRetypedPassword] = useState("");
	const [username, setUsername] = useState("");
	const [currentUsers, setCurrentUsers] = useState([]);
	const [deviceList, setDeviceList] = useState([]);
	const [radioValue, setradioValue] = useState("po");
	const [ssid, setSSID] = useState("");
	const [wifi_pass, setWifiPass] = useState("");
	const [machineName, setMachineName] = useState("");
	const [sensorType, setsensorType] = useState("sd");
	const [organization, setOrganization] = useState("");
	const location = useLocation();
	const [allUsers, setAllUsers] = useState([]);

	useEffect(() => {
		offsetBody();
		if (organization && organization !== "") {
			getAllUsers(props, setAllUsers, organization);
			fetchMachineNames(props, setDeviceList);
		}
	}, [organization]);

	useEffect(() => {
		loginStausChecker(props, navigate, {
			setOrganization,
			setUsername,
			location,
		});
	});

	function change_password() {
		const element = document.getElementById("cp_message");
		element.innerText = "Please wait";
		element.style.display = "block";
		element.style.color = "orange";
		if (newPassword == "") {
			element.innerText = "Please enter a valid password";
			element.style.display = "block";
			element.style.color = "red";
		} else if (newPassword === retypedPassword) {
			console.log("Here " + username);
			props.axios_instance
				.patch("/api/login/changePassword", {
					username,
					currentPassword,
					newPassword,
					organization,
				})
				.then((res) => {
					if (res.status == 200) {
						element.innerText = "Changed Successfully !!!";
						element.style.display = "block";
						element.style.color = "green";
					}
				})
				.catch((res) => {
					if (res.response.status == 401) {
						element.innerText = "Wrong Password";
						element.style.display = "block";
						element.style.color = "red";
					}
				});
		} else {
			element.innerText = "Password Mismatch";
			element.style.display = "block";
			element.style.color = "red";
		}
	}

	function unBlockUser(event) {
		const data = JSON.parse(event.target.getAttribute("data"));
		console.log(data);
		props.axios_instance
			.post("/api/unblockUsers", { username: data.username, organization })
			.then((res) => {
				if (res.status == 200) {
					const element = document.getElementById("table_status");
					element.style.display = "block";
					element.style.color = "green";
					element.innerText = "Unblocked the User";
				}
				getAllUsers(props, setAllUsers, organization);
			})
			.catch((err) => {
				const element = document.getElementById("table_status");
				element.style.display = "block";
				element.style.color = "red";
				element.innerText =
					err.response.data.message !== undefined
						? err.response.data.message
						: "Oops Somthing went wrong";
				console.log(err);
			});
	}

	function blockUser(event) {
		console.log(event.target.getAttribute("data"));
		const data = JSON.parse(event.target.getAttribute("data"));
		props.axios_instance
			.delete("/api/block/user", {
				data: { username: data.username, organization },
			})
			.then((res) => {
				props.axios_instance.get(props.server + "/getNewUsers").then((res) => {
					// setNewUserList(res.data);
				});
				if (res.status == 200) {
					const element = document.getElementById("table_status");
					element.style.display = "block";
					element.style.color = "orange";
					element.innerText = "Blocked the user Successfully";
				}
				getAllUsers(props, setAllUsers, organization);
			})
			.catch((err) => {
				const element = document.getElementById("table_status");
				element.style.display = "block";
				element.style.color = "red";
				element.innerText = err.response.data.message;
			});
	}

	function addSensorClicked(event) {
		let old_data = undefined;
		let new_data = undefined;
		if (event.key === "Enter") {
			old_data = JSON.parse(event.target.getAttribute("data"));
			new_data = event.target.value;
			event.target.value = "";
			console.log("entered");
		} else if (event.target.nodeName === "BUTTON") {
			old_data = JSON.parse(event.target.getAttribute("data"));
			new_data = event.target.parentNode.querySelector("input").value;
			event.target.parentNode.querySelector("input").value = "";
		}
		if (event.target.nodeName === "BUTTON" || event.key === "Enter") {
			old_data.sensorType += `,${new_data}`;
			old_data.organization = organization;
			console.log({ old_data });
			addSensor(
				props,
				{
					sensors: old_data.sensorType,
					machineName: old_data.machineName,
					organization,
				},
				setDeviceList
			);
		}
	}

	function removeSensorClicked(event) {
		const data = JSON.parse(event.target.getAttribute("data"));
		data.sensorType = data.sensorType.replace(
			"," + event.target.getAttribute("sensor"),
			""
		);
		data.sensorType = data.sensorType.replace(
			event.target.getAttribute("sensor"),
			""
		);
		addSensor(
			props,
			{
				sensors: data.sensorType,
				machineName: data.machineName,
				organization,
			},
			setDeviceList
		);
	}

	function addSensorActive(event) {
		console.log(event);
	}

	function removeDevice(event) {
		props.axios_instance
			.delete("/api/remove/machine", {
				data: { machineName: event.target.getAttribute("data"), organization },
			})
			.then((res) => {
				const element = document.getElementById("table_status3");
				element.style.display = "block";
				element.style.color = "orange";
				element.innerText = "Device removed Successfully !!!";

				fetchMachineNames(props, setDeviceList);
			})
			.catch((err) => {
				console.log(err);
				const element = document.getElementById("table_status3");
				element.style.display = "block";
				element.style.color = "red";
				element.innerText = err.response.data.message;
			});
	}

	function generatecodeClicked() {
		if (machineName != "") {
			props.axios_instance
				.post("/addDevice", {
					machineName: machineName,
					ssid: ssid,
					password: wifi_pass,
					sensorType: sensorType,
				})
				.then((res) => {
					if (res.status == 200) {
						fileDownload(res.data, "ArduinoSD.ino");
					}

					props.axios_instance.get("/fetch/machineNames").then((res) => {
						console.log(res);
						setDeviceList(res.data);
					});
				})
				.catch((err) => {
					console.log(err);
					if (err.response.status == 409) {
						const element = document.getElementById("gen_error");
						element.style.display = "block";
						element.style.color = "red";
						element.innerText = "Machine name is already in use";
						console.log("Machine name is already in use");
					} else {
						const element = document.getElementById("gen_error");
						element.style.display = "block";
						element.style.color = "red";
						element.innerText = "Error code : " + err.response.status;
					}
				});
		} else {
			const element = document.getElementById("gen_error");
			element.style.display = "block";
			element.style.color = "red";
			element.innerText = "Please enter a valid machine name";
		}
	}

	return (
		<div>
			<NavBar visible="true" title={organization} username={username} />
			<div className="container">
				<div className="cp">
					<h1 className="subtitle">Change Password</h1>

					<div>
						<label>Current Password</label>
						<input
							value={currentPassword}
							onChange={(event) => setCurrentPassword(event.target.value)}
							type="password"
						></input>
					</div>

					<div>
						<label>New Password</label>
						<input
							value={newPassword}
							onChange={(event) => setNewPassword(event.target.value)}
							type="password"
						></input>
					</div>

					<div>
						<label>Retype Password</label>
						<input
							value={retypedPassword}
							onChange={(event) => setRetypedPassword(event.target.value)}
							type="password"
						></input>
					</div>

					<button
						onClick={change_password}
						className="my-button change_password"
					>
						Change
					</button>
					<p id="cp_message">Changed Successfully !!</p>
				</div>
			</div>
			<div className="container">
				<div className="nu">
					<h1 className="subtitle">All Users</h1>
					{allUsers.length == 0 ? (
						<h3 className="no_data">No new Requests found</h3>
					) : (
						<>
							<table className="table">
								<thead>
									<tr>
										<td>Username</td>
										<td>Role</td>
										<td>Actions</td>
									</tr>
								</thead>
								<tbody>
									{allUsers.map((data) => {
										return (
											<tr>
												<td>{data.username}</td>
												<td>{data.role}</td>
												<td>
													{data.isActive ? (
														<button
															data={JSON.stringify(data)}
															className="table_button my-button"
															onClick={blockUser}
															disabled={username === data.username}
														>
															
														</button>
													) : (
														<button
															data={JSON.stringify(data)}
															className="table_button my-button"
															onClick={unBlockUser}
														>
															&#10004;
														</button>
													)}
												</td>
											</tr>
										);
									})}
								</tbody>
							</table>
						</>
					)}
					<p id="table_status"></p>
				</div>
			</div>

			<div className="container">
				<div className="dl">
					<h1 className="subtitle">Devices Available</h1>
					{deviceList.length == 0 ? (
						<h3 className="no_data">No devices found</h3>
					) : (
						<>
							<table className="table">
								<thead>
									<tr>
										<td>Username</td>
										<td>Sensors</td>
										<td>Actions</td>
									</tr>
								</thead>
								<tbody>
									{deviceList.map((data) => {
										return (
											<tr className="tablerow">
												<td style={{ fontWeight: 900 }}>
													{data["machineName"]}
												</td>
												{/* <td>{data["sensorType"]}</td> */}
												<td className="middle-column">
													<span className="sensors-column">
														{data.sensorType.split(",").map((sensor) => {
															return (
																<span className="sensor-individual-holder">
																	<p className="sensor-name-holder">{sensor}</p>
																	<button
																		className="remove-sensor-button"
																		onClick={removeSensorClicked}
																		sensor={sensor}
																		data={JSON.stringify(data)}
																	>
																		
																	</button>
																</span>
															);
														})}
													</span>
													<span className="add-sensor-holder">
														<input
															className="sensor-input"
															placeholder="Add Sensor"
															onKeyDown={addSensorClicked}
															data={JSON.stringify(data)}
														></input>
														<button
															className="sensor-input"
															data={JSON.stringify(data)}
															onClick={addSensorClicked}
														>
															
														</button>
													</span>
												</td>
												<td className="action-column">
													<button
														data={data["machineName"]}
														disabled={data.username === "admin"}
														className="table_button my-button"
														onClick={(event) => removeDevice(event)}
													>
														&#10060;
													</button>
												</td>
											</tr>
										);
									})}
								</tbody>
							</table>
						</>
					)}
					<p id="table_status3"></p>
				</div>
			</div>

			<div className="container">
				<div className="cg">
					<h1 className="subtitle">Add Device</h1>
					<div className="machinename">
						<label>Machine Name : </label>
						<input
							type="text"
							onChange={(event) => setMachineName(event.target.value)}
						></input>
					</div>

					<div className="wifitype">
						<label>Select Wifi Type : </label>
						<span>
							<label htmlFor="po">Only Password</label>
							<input
								name="wifitype"
								id="po"
								type="radio"
								checked={"po" === radioValue}
								onChange={() => setradioValue("po")}
							></input>
						</span>
						<span>
							<label htmlFor="up">Username and Password</label>
							<input
								name="wifitype"
								id="up"
								checked={"up" === radioValue}
								type="radio"
								onChange={() => setradioValue("up")}
							></input>
						</span>
					</div>
					<div>
						{radioValue == "up" ? (
							<>
								<p className="wc">
									Most of the wifi asks only for password like WEP or WPA or
									WPA2 But private wifi which are called as CLASS - A wifi
									implements some firewall which asks for username and password.
									This username and password must be sent as POST or GET request
									it depends on the organization. This wifi of wifi must
									implemented by the user it cant be pre-deremined
								</p>
							</>
						) : (
							<>
								<div className="ssid_c">
									<label>SSID</label>
									<input
										type="text"
										onChange={(event) => setSSID(event.target.value)}
									></input>
								</div>
								<div className="pass_c">
									<label>Password</label>
									<input
										type="password"
										onChange={(event) => setWifiPass(event.target.value)}
									></input>
								</div>
							</>
						)}
					</div>
					<div className="wifitype">
						<label>Select sensorType : </label>
						<span>
							<label htmlFor="static_data">Static Sensor Data</label>
							<input
								type="radio"
								id="static_data"
								checked={"sd" == sensorType}
								onChange={() => setsensorType("sd")}
							></input>
						</span>

						<span>
							<label htmlFor="picks_place">Picks and Place</label>
							<input
								type="radio"
								id="picks_place"
								checked={"pp" == sensorType}
								onChange={() => setsensorType("pp")}
							></input>
						</span>
					</div>

					<button
						className="generate_button my-button"
						onClick={generatecodeClicked}
					>
						Generate
					</button>
					<p id="gen_error"></p>
				</div>
			</div>
		</div>
	);
}

export default ControlPanel;
